html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

a {
  color: #000;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5e311e;
}

h1 {
  margin: 0 0 2.5rem;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h2, .heading-white {
  margin: 0 0 2.5rem;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h3 {
  margin: 0 0 2.5rem;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-white {
  font-size: 3.2rem;
  color: #5e311e;
}
h2 span, .heading-white span {
  display: block;
  font-size: 1.7rem;
  color: #c39c63;
}
h2::after, .heading-white::after {
  content: "";
  display: block;
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
  background-image: url(../assets/img/icons/cafe.svg);
  background-size: 8rem;
}

.heading-white {
  color: #fff;
}
.heading-white span {
  color: #fff;
}
.heading-white::after {
  display: none;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #000;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: #000;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header__home {
  background-image: url(../assets/img/headers/header_inicio.jpg);
}
.header__about {
  background-image: url(../assets/img/headers/header_nosotros.jpg);
}
.header__process {
  background-image: url(../assets/img/headers/header_proceso.jpg);
}
.header__menu {
  background-image: url(../assets/img/headers/header_menu.jpg);
}
.header__gallery {
  background-image: url(../assets/img/headers/header_galeria.jpg);
}
.header__contact {
  background-image: url(../assets/img/headers/header_contacto.jpg);
}
.header__contenedor {
  position: relative;
}
.header__titulo {
  margin-top: 2rem;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}
@media (min-width: 768px) {
  .header__titulo {
    margin-top: 10rem;
  }
}

.header__barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__barra .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__barra .logo {
    width: 15rem;
    margin: 0;
  }
}

.main {
  background-color: #fff;
  padding: 2rem;
}
@media (min-width: 768px) {
  .main {
    position: relative;
    margin-top: -10rem;
    padding: 10rem;
  }
}

.footer {
  background-color: #5e311e;
}
.footer__contenedor {
  padding: 5rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .footer__contenedor {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__titulo {
  font-size: 2.4rem;
  font-family: "Bona Nova", serif;
  color: #c39c63;
  margin: 0 0 1rem;
}
.footer__copy {
  padding: 3rem;
  text-align: center;
  color: #fff;
  margin: 0;
}

.footer__box {
  margin-bottom: 5rem;
  text-align: center;
  font-family: "Bona Nova", serif;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__box p,
.footer__box a {
  font-size: 2rem;
  margin: 1rem;
}

.hamburguer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3rem 0 0 0;
  padding: 0;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .hamburguer {
    display: none;
  }
}
.hamburguer__icon {
  text-align: center;
  height: 5rem;
  padding: 0.5rem;
  color: #fff;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 0.5rem;
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: 0.3s all ease-in-out;
}
.nav-active {
  height: 30vh;
  pointer-events: auto;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    margin-top: 0;
    height: auto;
    overflow: visible;
    pointer-events: auto;
  }
}
.nav__link {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  transition: 0.2s ease-in-out;
}
.nav__link-active {
  color: #c39c63;
}
@media (hover: hover) {
  .nav__link:hover {
    color: #c39c63;
  }
}

@media (min-width: 768px) {
  .icons__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2;
  }
}

.icon__img {
  margin: 0 auto;
  width: 12rem;
}
.icon__text {
  margin: 2rem;
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5e311e;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery__img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .gallery__picture:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery__picture:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .gallery__picture:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery__picture:nth-child(2) img {
    width: 62rem;
  }
  .gallery__picture:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery__picture:nth-child(4) img {
    height: 62rem;
  }
  .gallery__picture:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery__picture:nth-child(5) img {
    width: 62rem;
  }
  .gallery__picture:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery__picture:nth-child(6) img {
    width: 62rem;
  }
  .gallery__picture:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery__picture:nth-child(7) img {
    height: 62rem;
  }
  .gallery__picture:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery__picture:nth-child(8) img {
    width: 94rem;
  }
  .gallery__picture:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery__picture:nth-child(9) img {
    width: 62rem;
  }
  .gallery__picture:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery__picture:nth-child(10) img {
    width: 62rem;
  }
}

.menu {
  padding: 15rem 2rem 20rem;
  background-image: url(../assets/img/icons/menu_arriba.svg), url(../assets/img/icons/menu_abajo.svg), linear-gradient(to top, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 95%, 100%;
  background-size: 200%;
}
@media (min-width: 768px) {
  .menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .menu {
    background-size: 50%, 50%, 100%;
  }
}
@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.menu__section-titulo {
  color: #fff;
}

.menu__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__item p {
  margin: 0 0 2rem 0;
  color: #fff;
}
.menu__item-description {
  font-size: 2.2rem;
  line-height: 1.5;
}
.menu__item-subdescription {
  display: block;
  color: #c39c63;
}
.menu__item-price {
  font-size: 4rem;
  font-weight: 700;
}

.testimonial {
  margin: 0 auto;
  padding: 2rem 4rem;
  max-width: 50rem;
  background-color: #ebe7de;
  border-radius: 2rem;
}
.testimonial__autor {
  font-family: "Bona Nova", serif;
  font-size: 2.4rem;
  font-weight: 700;
  color: #5e311e;
  text-align: right;
}

.icons {
  margin-top: 5rem;
}

.experience {
  margin-top: 3rem;
}

.testimonials {
  margin-top: -9rem;
  padding: 2rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 10rem;
  }
}

@media (min-width: 1024px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
.about__img {
  margin-bottom: 5rem;
  transform: rotate(-6deg);
  border: 3rem solid #fff;
  box-shadow: 0.9rem 1rem 0.5rem -0.7rem rgba(0, 0, 0, 0.35);
}
@media (min-width: 1024px) {
  .about__img {
    margin-bottom: 0;
  }
}
.about__quote {
  margin: 0;
  padding: 1rem;
  font-size: 2rem;
  border-left: 0.5rem solid #5e311e;
}

.process {
  margin-bottom: 2rem;
}
.process:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .process {
    display: flex;
    align-items: center;
    gap: 5rem;
  }
}
.process:nth-child(even) {
  flex-direction: row-reverse;
}
.process__picture, .process__text {
  flex: 1;
}
.process__text {
  margin-top: 1rem;
}
.process__titulo {
  margin: 0;
  text-align: left;
  color: #5e311e;
}
.process__titulo::after {
  content: "";
  display: block;
  width: 50%;
  height: 0.5rem;
  background-color: #c39c63;
}

.main__menu {
  padding-bottom: 0;
}

.menu-contenido {
  background-image: url(../assets/img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .menu-contenido {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .menu-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    aspect-ratio: 16/6;
    background-size: 50%;
    background-position: bottom right;
  }
}
.menu-contenido__parrafo {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../assets/img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.form__camp {
  margin-bottom: 2rem;
}
.form__camp:last-of-type {
  margin-bottom: 0;
}
.form__label {
  display: block;
  margin: 1rem;
  font-size: 2.2rem;
  font-weight: 700;
  color: #5e311e;
  text-transform: uppercase;
}
.form__input {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5e311e;
  border-radius: 1rem;
}
.form input[type=submit] {
  width: 100%;
  padding: 1rem;
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #5e311e;
  border: none;
  border-radius: 1rem;
  transition: 0.2s all ease;
}
@media (hover: hover) {
  .form input[type=submit]:hover {
    cursor: pointer;
    background-color: #c39c63;
  }
}