@use "config/mixins" as m;

.header__barra {
  display: flex;
  flex-direction: column;

  @include m.tablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .logo {
    width: 50%;
    margin: 0 auto;

    @include m.tablet {
      width: 15rem;
      margin: 0;
    }
  }
}
